<template>
  <div class="container" :class="{ disabled: notes.length > 0 }">
    <button class="btn menu-btn" :class="{ invert: !open }" @click="open = !open">
      <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" height="24" viewBox="0 0 24 24" width="24">
        <path d="M0 0h24v24H0z" fill="none"/>
        <path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"/>
      </svg>
    </button>
    <div class="sidebar" :class="{open}">
      <ul class="note-list">
        <a
          v-for="note in notes"
          class="note"
          :class="{ active: note.id === id }"
          @click="setId(note.id)"
          :href="'#' + note.id"
        >
          {{note.title}}
        </a>
      </ul>
      <button class="btn add-btn" @click="setId()">
        <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" height="24" viewBox="0 0 24 24" width="24">
          <path d="M0 0h24v24H0z" fill="none"/>
          <path d="M14 2H6c-1.1 0-1.99.9-1.99 2L4 20c0 1.1.89 2 1.99 2H18c1.1 0 2-.9 2-2V8l-6-6zm2 14h-3v3h-2v-3H8v-2h3v-3h2v3h3v2zm-3-7V3.5L18.5 9H13z"/>
        </svg>
      </button>
    </div>
  </div>
</template>

<script>
import eventHub from '../eventHub'

export default {
  data () {
    return {
      open: window.innerWidth >= 1500
    }
  },
  computed: {
    smallScreen: () => window.innerWidth < 1500
  },
  props: {
    id: String,
    notes: Array
  },
  created () {
    eventHub.$on('close-menu', this.close)
  },
  methods: {
    close () {
      this.open = false
    },
    setId (id = Date.now().toString()) {
      this.$emit('setId', id)
    }
  }
}
</script>

<style>
.sidebar {
  background: #42b983;
  color: white;
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 2vw 0 0;
  width: 240px;
  transition: transform 0.2s ease;
  transform: translate3d(-100%, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.open {
  transform: translate3d(0, 0, 0);
  box-shadow: 4px 0 32px -4px rgba(27, 75, 53, 0.6);
}

.disabled:not(:hover) {
  animation-name: fadescale;
  animation-duration: 8s;
  animation-fill-mode: forwards;
}

@keyframes fadescale {
  50% {
    filter: grayscale(0.5) brightness(1.1);
  }
  100% {
    filter: grayscale(1) brightness(1.4);
  }
}

.note-list {
  padding: 0;
  box-shadow: 0 -4px 8px -1px #42b983 inset;
  overflow: auto;
}

.note {
  display: block;
  border-top: solid 1px #eee;
  padding: 1em;
  line-height: 1.5;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-decoration: none;
  color: white;
}

.btn {
  padding: 0.25em;
  border: none;
  background: none;
  font-size: 36px;
  color: white;
  text-shadow: -2px 1px 6px rgba(20, 20, 20, 0.4);
}

.btn:focus {
  outline: none;
  text-shadow: -4px 1px 8px rgba(20, 20, 20, 0.4);
}

.menu-btn {
  z-index: 30;
  position: fixed;
  bottom: 0;
  left: 0;
  transition: color 0.15s;
}

.invert {
  color: #333;
}

.add-btn {
  left: 180px;
  flex-shrink: 0;
}
</style>



