<template>
  <form class="textarea-wrapper">
    <textarea
      ref="textarea"
      v-model="note.content"
      @focus="handleFocus"
      @input="updateHeight"
      @keyup="saveNote"
      placeholder=""
      aria-label="note"
      required
    ></textarea>
    <button type="button" class="btn copy-btn" @click="copyText">
      <svg xmlns="http://www.w3.org/2000/svg" fill="#000000" height="24" viewBox="0 0 24 24" width="24">
        <path d="M0 0h24v24H0z" fill="none"/>
        <path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm3 4H8c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h11c1.1 0 2-.9 2-2V7c0-1.1-.9-2-2-2zm0 16H8V7h11v14z"/>
      </svg>
    </button>
  </form>
</template>

<script>
export default {
  name: 'notepad',
  props: {
    note: Object,
    saveNote: Function
  },
  methods: {
    handleFocus () {
      this.$emit('focused')
    },
    updateHeight () {
      const ta = document.querySelector('textarea')
      ta.style.height = 'auto'
      ta.style.height = `${ta.scrollHeight}px`
    },
    copyText () {
      var ta = document.createElement('textarea')
      ta.style = {
        ...ta.style,
        position: 'absolute',
        left: 0,
        top: 0
      }
      document.body.appendChild(ta)
      ta.value = this.note.content
      ta.focus()
      ta.select()
      try {
        var successful = document.execCommand('copy')
        if (successful) {
          console.log('Copied!')
          ta.parentElement.removeChild(ta)
          document.querySelector('textarea').focus()
        } else {
          throw new Error('copy failed')
        }
      } catch (err) {
        console.error(err)
      }
      // Remove the selections - NOTE: Should use
      // removeRange(range) when it is supported
      window.getSelection().removeAllRanges()
    }
  }
}
</script>

<style>
.textarea-wrapper {
  padding: 1em 1em 56px 1em;
  max-width: 64em;
  margin: 0 auto;
  width: 100%;
  height: 100vh;
  position: relative;
}

.textarea-wrapper:invalid textarea {
  border: solid 1px rgba(230, 230, 230, 0);
}

textarea {
  border: solid 1px rgba(22, 22, 22, 0.01);
  box-shadow: 3px 3px 120px -4px rgba(33, 33, 33, 0.25);
  width: 100%;
  min-height: 100vh;
  padding: 4vw;
  font-size: calc(1em + 0.5vw);
  line-height: 1.4;
  transition: box-shadow 0.3s cubic-bezier(0.4, 0, 1, 1);
  border-radius: 2px;
  overflow: hidden;
}

textarea:focus {
  outline: none;
  box-shadow: 1px 1px 72px -1px rgba(33, 33, 33, 0.25);
}

.textarea-wrapper:not(:invalid) textarea:focus {
  box-shadow: none;
}

.copy-btn {
  position: fixed;
  right: 0;
  bottom: 0;
}
</style>



