<template>
  <div id="app">
    <notepad :note="note" :saveNote="saveNote" v-on:focused="focused"></notepad>
    <sidebar :notes="notes" :id="id" v-on:setId="setId"></sidebar>
  </div>
</template>

<script>
import Notepad from './components/Notepad'
import Sidebar from './components/Sidebar'
import eventHub from './eventHub'
import { getNote, getAllNotes, updateNote } from './Note'

const initialId = localStorage.getItem('currentNoteId') || Date.now().toString()

export default {
  name: 'app',
  data () {
    return {
      id: initialId,
      notes: getAllNotes() || []
    }
  },
  computed: {
    note: {
      get () {
        return getNote(this.id)
      }
    }
  },
  components: {
    Notepad,
    Sidebar
  },
  methods: {
    saveNote (evt) {
      const note = getNote(this.id)
      note.content = evt.target.value
      this.notes = updateNote(note)
    },
    setId (id) {
      this.id = id
      localStorage.setItem('currentNoteId', id)
      document.querySelector('textarea').focus()
    },
    focused () {
      if (window.matchMedia('(max-width: 1500px)').matches) eventHub.$emit('close-menu')
    }
  }
}
</script>

<style>
*,
*:before,
*:after {
  box-sizing: border-box;
}

#app,
textarea {
  font-family: 'Merriweather', Georgia, Times, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



